  --font-family-math: Source Serif Variable, Source Serif, serif;
  --font-family-code: Source Code Variable, Source Code, monospace;
  --font-family-serif: Source Serif Variable, Source Serif, serif;
  --font-family-sans-serif: Source Sans Variable, Source Sans, sans-serif;
  --font-family-monospace: Source Code Variable, Source Code, monospace;
}

:root {
  --color-background: #ffffff;
  --color-background-inverse: #262626;
  --color-border: #e6e6e6;
  --color-text: #262626;
  --color-text-inverse: #ffffff;
  --color-link: #737373;
  --color-element-background: #e6e6e6;
  --color-element-disabled: #cccccc;
  --color-element-enabled: #737373;
  --color-element-selected: #999999;
