  .dec-html {
    font-size: 0.8rem;
  }
}
.dec-body {
  min-height: 100%;
  font-size: 1.125rem;
  background-color: #e6e6e6;
}

.dec-nav-brand:hover {
  text-decoration: underline;
}

.dec-nav-toggler {
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
  color: #b3b3b3;
}
@media (prefers-reduced-motion: reduce) {
  .dec-nav-toggler {
    transition: none;
  }
}
.dec-nav-toggler:focus {
  color: #e6e6e6;
  outline: revert;
  box-shadow: none;
}
.dec-nav-toggler:hover {
  color: #e6e6e6;
}
.dec-nav-toggler[aria-expanded=true] {
  color: #ffffff;
}

.dec-nav-link:hover {
  text-decoration: underline;
}

