  box-shadow: 1.5px 3px 1px -2px rgba(0, 0, 0, 0.4), 1px 2px 2px 0px rgba(0, 0, 0, 0.34), 0.5px 1px 5px 0px rgba(0, 0, 0, 0.32);
}
